import { merge } from 'merge-anything';


function handleStruture(flwStruture) {

  const fn = `function run(estado, input, objetoNegrito, acaoCanal, saudacao, contact, canal, name) {
  
      try{ 
  
      const { negrito, negrito2 } = JSON.parse(objetoNegrito);
  
      if (name) {
  
              name = capitalizeFirstLetter(name.split(' ')[0]);
  
      } else {
  
              name = ''
      }
  
      let estadoCanal = canal != 'WhatsApp' ? 'solicita_telefone' : 'boas_vindas';  
  
      const estrutura = ${flwStruture};
  
    const novoEstado = estrutura.respostas.filter(e => e.estado === estado);
  
    const novoEstadoAux = novoEstado;
  
    if (estado === 'solicita_email') {
  
      if (validaEmail(input)) {
  
        const obj = estrutura.respostas.filter(e => e.estado === 'solicita_email')[0];
        obj.emailValido = true;
        return obj;
  
      } else {
  
        const obj = estrutura.respostas.filter(e => e.estado === 'solicita_email_invalido')[0];
        obj.emailValido = false;
        return obj
      }
    }
  
    if (estado === 'solicita_cpf') {
  
      if (validaDocumento(input)) {
  
        const filtroCanal = canal == 'WhatsApp' ? 'solicita_cpf' : 'solicita_cpf'
        const obj = estrutura.respostas.filter(e => e.estado === filtroCanal)[0];
        obj.validaCpf = true;
        return obj;
  
      } else {
  
        return estrutura.respostas.filter(e => e.estado === 'solicita_cpf_invalido')[0];
      }
    }
    if (estado === 'solicita_telefone') {
  
      if (validaTelefone(input) !== "TELEFONE INVALIDO") {
  
        const obj = estrutura.respostas.filter(e => e.estado === 'solicita_telefone')[0];
        obj.telefoneValido = true;
        return obj;
  
      } else {
  
        const obj = estrutura.respostas.filter(e => e.estado === 'telefone_invalido')[0];
        obj.telefoneValido = false;
        return obj;
  
      }
    }
        if (estado && !novoEstado[0].inputs.includes(input)) {
  
        let arraySplitado = [];
    
          novoEstado[0].inputText.forEach(e => {
            arraySplitado.push({ id: e.split('-')[1], texto: e.split('-')[0] });
          });
    
          const id = arraySplitado.filter(e => e.texto.toLowerCase() == input.toLowerCase());
          return id.length > 0 ? novoEstado[0].resposta.filter(e => e.id == id[0].id)[0] :  novoEstadoAux[0].estado.includes('aguarda_input_') ? 
          estrutura.respostas.filter(e => e.estado === novoEstadoAux[0].estadoDestino )[0] : novoEstadoAux[0];
    
      } else {
        return novoEstado[0].inputs.includes(input) ? 
                  novoEstado[0].resposta.filter(e => e.id == input)[0] : false; 
      }
   } catch(e) {
      return e;
   }
   }
  
   function validaDocumento(documento) {
  
    documento = documento.replace(/\D/g, '');
    return documento.length > 11 ? verifyDoc()["validCnpj"](documento) : verifyDoc()["validCpf"](documento);
  
   }
   function verifyDoc(documento1) {
  
    return {
      validCpf: function (cpf) {
        if (!cpf || cpf.length != 11
          || cpf == "00000000000"
          || cpf == "11111111111"
          || cpf == "22222222222"
          || cpf == "33333333333"
          || cpf == "44444444444"
          || cpf == "55555555555"
          || cpf == "66666666666"
          || cpf == "77777777777"
          || cpf == "88888888888"
          || cpf == "99999999999")
          return false
        var soma = 0
        var resto
        for (var i = 1; i <= 9; i++)
          soma = soma + parseInt(cpf.substring(i - 1, i)) * (11 - i)
        resto = (soma * 10) % 11
        if ((resto == 10) || (resto == 11)) resto = 0
        if (resto != parseInt(cpf.substring(9, 10))) return false
        soma = 0
        for (var i = 1; i <= 10; i++)
          soma = soma + parseInt(cpf.substring(i - 1, i)) * (12 - i)
        resto = (soma * 10) % 11
        if ((resto == 10) || (resto == 11)) resto = 0
        if (resto != parseInt(cpf.substring(10, 11))) return false
        return true
      },
      validCnpj: function (cnpj) {
        if (!cnpj || cnpj.length != 14
          || cnpj == "00000000000000"
          || cnpj == "11111111111111"
          || cnpj == "22222222222222"
          || cnpj == "33333333333333"
          || cnpj == "44444444444444"
          || cnpj == "55555555555555"
          || cnpj == "66666666666666"
          || cnpj == "77777777777777"
          || cnpj == "88888888888888"
          || cnpj == "99999999999999")
          return false
        var tamanho = cnpj.length - 2
        var numeros = cnpj.substring(0, tamanho)
        var digitos = cnpj.substring(tamanho)
        var soma = 0
        var pos = tamanho - 7
        for (var i = tamanho; i >= 1; i--) {
          soma += numeros.charAt(tamanho - i) * pos--
          if (pos < 2) pos = 9
        }
        var resultado = soma % 11 < 2 ? 0 : 11 - soma % 11
        if (resultado != digitos.charAt(0)) return false;
        tamanho = tamanho + 1
        numeros = cnpj.substring(0, tamanho)
        soma = 0
        pos = tamanho - 7
        for (var i = tamanho; i >= 1; i--) {
          soma += numeros.charAt(tamanho - i) * pos--
          if (pos < 2) pos = 9
        }
        resultado = soma % 11 < 2 ? 0 : 11 - soma % 11
        if (resultado != digitos.charAt(1)) return false
        return true;
      }
    }
   }
  
   function validaTelefone(phoneText) {
  
    var RegExp = /^(([1-2][0-8])(9\d{4})\d{4})|(((1[2-9]{1})|([2-9]{1}\d{1}))[5-9]\d{3}\d{4})$/
     if(RegExp.test(phoneText))
       return true
    return "TELEFONE INVALIDO";
  
   }
  
   function validaEmail(emailText) {
  
    let emailsFound = [];
    var words = emailText.split(' ');
  
    for (i = 0; i < words.length; i++) {
      var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (regex.test(String(words[i]).toLowerCase())) {
        emailsFound.push(words[i]);
      }
    }
  
    if (emailsFound.length > 0) {
      return !!emailsFound[0];
    }
    else {
      return false;
    }
  
   }
  
   function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
   }
   `
  return fn;
}

function handleFlowReplaced(flwReplaced) {

  const replaceFlow = JSON.stringify(flwReplaced).toString()
    .replace(/"{{/g, '`{{')
    .replace(/}}"/g, '}}`')
    .replace(/:"`/g, ':`')
    .replace(/`",/g, '`,')
    .replace(/`"}/g, '`}')
    .replace(/"\${/g, '`${')
    .replace(/}"/g, '}`')
  return replaceFlow;

}

function handleFlowReplacedToObject(flwReplacedToObject) {

  let obj = flwReplacedToObject.replace(/`{{/g, '"{{').replace(/}}`/g, '}}"');
  let obj2 = obj.replace(/`/g, '"').replace(/`/g, '"');
  let parse = JSON.parse(JSON.stringify(obj2))
  return parse;
}



function handleFlowMerge(flwBefore, flwLater, flag) {

  const t = handleFlowReplacedToObject(JSON.stringify(flwLater))

  const e = flwBefore.split('//->')[1].split('//<-')[0].split('=')[1].split(';')[0]
  const removeT = e.replace(/\t/g, '').replace(/\n/g, '')
  const backToObject = handleFlowReplacedToObject(removeT)


  const obj1 = JSON.parse(t);
  const obj2 = JSON.parse(backToObject);

  const mergeFlow = merge(obj2, obj1);


  const newFlw = handleFlowReplaced(mergeFlow);

  return flag === true ? { flwNow: mergeFlow, newFlw } : { structure: handleStruture(newFlw), newFlw: mergeFlow };

}
export default {
  handleFlowMerge
}