import api from '../baseUrl';

export default (guid, key, phone) => {

 
  return api.post('/commands', {
    "method": "get",
    "uri": `/contacts?$skip=0&$take=100&$filter=(source%20ne%20'blip.ai'%20or%20source%20eq%20null)%20and%20(startswith(phonenumber%2C'55${phone}'))`,
    "id": guid
  }, {
    headers: {
      'Content-Type': 'application/json',
      'Authorization': key
    }
  });
}