import React from 'react';


function TextPreview({ data }) {


  return (

    <>
    
     <label>Text Preview</label>

      <div class="left">
        <div class="blip-card-container">
          <div class="blip-card">
            <div class="blip-container plain-text">
              <div class="bubble left">
                <div>{data}</div>
             
              </div>
            </div>
          </div>
        </div>
      </div>
    </>

  )

}


export default TextPreview;