import React, { useState, useEffect } from 'react';


function MenuPreview({ data, text }) {

  const [inputList, setInputList] = useState([{ opcao: "", estadoDestino: "", menu: false, valida: false }]);

  console.log(data)
  useEffect(() => {
    setInputList([...data])

  }, [])



  return (

    <>

      <div class="left">
      <label>Menu Preview </label>
        <div class="blip-card-container">
          <div class="blip-container select blip-card">
            <div class="">
              <div class="bubble left">

                <div class="text-left">{text}</div>
                <div class="fixed-options disable-selection">
                  <ul>
                    {data.map((x, i) => {

                      let color = x.opcao.length > 20 ? 'red' : '' ;
                    
                      return (

                        <li>
                          <div style={{color: color}}>{x.opcao}</div>
                        </li>

                      )

                    })}
                  </ul>
                </div>
                
              </div>
            </div>
          </div>
        </div>
      </div>
    </>

  )

}


export default MenuPreview;