import React, { useState } from 'react';
import { NotificationContainer } from 'react-notifications';
import handleGetOrigin from '../../services/handleGetOrigin';
import handleSetPublished from '../../services/handleSetPublished';
import handleSetWorkingConfiguration from '../../services/handleSetWorkingConfiguration';
import handleGetWorkFlow from '../../services/handleGetWorkFlow';
import handleGetGlobais from '../../services/handleGetGlobais';
import handleGetGlobaisPublished from '../../services/handleGetGlobaisPublished';
import handleSetPublishedFlow from '../../services/handleSetPublishedFlow';
import handleGetWorkFlowPublished from '../../services/handleGetWorkFlowPublished';
import setGlobal from '../../services/handleSetGlobal';

import handleGetResourceAll from '../../services/handleGetResourceAll';
import handleGetResourceItems from '../../services/handleGetResourceItems';
import handleSetResource from '../../services/handleSetResource';

import handleGetRules from '../../services/handleGetRules';
import handleSetRules from '../../services/handleSetRules';

import handleGetReplies from '../../services/handleGetReplies';
import handleSetReplies from '../../services/handleSetReplies';

import handleGetQueueManagement from '../../services/handleGetQueueManagement';
import handleSetQueueManagement from '../../services/handleSetQueueManagement';

import getAttendant from '../../services/getAttendant';
import setAttendant from '../../services/setAttendant';

import { showToast } from "../../api/commomService"

import Switch from "react-switch";
import guid from '../../utils/guid';
import 'react-notifications/lib/notifications.css';
import { BlipLoading } from '../BlipLoading'
import { MomentProps } from "react-moment";

function Clone({ info }) {


  localStorage.removeItem('keyOne');
  const [keyOrigin, setKeyOrigin] = useState(localStorage.getItem('keyOne'));
  const [keyTarget, setKeyTarget] = useState(info);
  const [loading, setLoading] = useState(false)

  const [checkedResource, setCheckedResource] = useState(false);
  const handleChangeResource = nextChecked => {
    setCheckedResource(nextChecked);
  };

  const [checkedRules, setCheckedRules] = useState(false);
  const handleChangeRules = nextChecked => {
    setCheckedRules(nextChecked);
  };

  const [checkedAttendants, setCheckedAttendants] = useState(false);
  const handleCheckAttendants = nextChecked => {
    setCheckedAttendants(nextChecked);
  };


  const [checkedBuilder, setCheckedBuilder] = useState(false);
  const handleChangeBuilder = nextChecked => {
    setCheckedBuilder(nextChecked);
  };


  const [checkedReplies, setCheckedReplies] = useState(false);
  const handleChangeReplies = nextChecked => {
    setCheckedReplies(nextChecked);
  };




  async function start(event) {


    event.preventDefault();



    setLoading(true)
    if (!keyOrigin && !info) {
      showMessage('Data is missing. Please check the Source and Destination Keys!!', 'warning', 'Notice!');
      setLoading(false)
      return false;

    }

    if (!keyOrigin.includes('Key')) {
      showMessage('key typed, is not a valid key!', 'warning', 'Notice!');
      setLoading(false)
      return false;
    }

    if (!checkedBuilder && !checkedRules && !checkedResource && !checkedReplies && !checkedAttendants) {

      showMessage('Choose an option to clone!', 'warning', 'Notice!')
      setLoading(false)
      return false;
    }



    if (checkedBuilder) {

      const comeBackHandleGerOrigin = await handleGetOrigin(guid(), keyOrigin);
      const { data } = comeBackHandleGerOrigin;


      await handleSetPublished(guid(), data.resource, info);
      await handleSetWorkingConfiguration(guid(), data.resource, info);

      const comeBackHandleGetWorkFlow = await handleGetWorkFlow(guid(), keyOrigin);

      await handleSetPublishedFlow(comeBackHandleGetWorkFlow.data.resource, info);
      await handleGetWorkFlowPublished(guid(), comeBackHandleGetWorkFlow.data.resource, keyOrigin);

      const getGlobal = await handleGetGlobais(guid(), keyOrigin);
      await setGlobal(guid(), getGlobal.data.resource, info);

      const comeBckHandleGetGlobaisPublished = await
        handleGetGlobaisPublished(guid(), comeBackHandleGetWorkFlow.data.resource, info);



      if (comeBckHandleGetGlobaisPublished.status === 200) {

        showMessage('Your builder has been cloned successfully', 'success', 'Done!')
        setKeyOrigin('');

        setKeyTarget('');
        setCheckedBuilder(false)
        setLoading(false)

      } else {
        setCheckedBuilder(false)
        showMessage('Error cloning builder', 'danger', 'Error!')
      }
    }


    if (checkedResource) {

      const getResourceAll = await handleGetResourceAll(guid(), keyOrigin);

      if (getResourceAll.data.status !== 'failure') {

        getResourceAll.data.resource.items.forEach(async (items) => {

          const resultGetItems = await handleGetResourceItems(guid(), keyOrigin, items);
          await handleSetResource(guid(), info, resultGetItems.data.type, items, resultGetItems.data.resource);

        });

        showMessage('Your resources have been cloned successfully', 'success', 'Done!');
        setLoading(false)
        setCheckedResource(false)



      } else {

        showMessage('Error cloning your resources', 'danger', 'Notice!');
        setLoading(false)

      }
    }


    if (checkedRules) {

      const allRules = await handleGetRules(guid(), keyOrigin);

      const queueManagement = await handleGetQueueManagement(guid(), keyOrigin);

      if (queueManagement.data.status !== 'failure') {

        queueManagement.data.resource.items.forEach(async (rulesItems) => {
          await handleSetQueueManagement(guid(), rulesItems, info);
        });

        allRules.data.resource.items.forEach(async (rulesItems) => {
          await handleSetRules(guid(), rulesItems, info);
        });

        showMessage('Your rules have been cloned successfully', 'success', 'Done!');
        setLoading(false)
        setCheckedRules(false)

      } else {
        setLoading(false)
        showMessage('Error cloning your rules', 'danger', 'Notice!');
      }

    }

    if (checkedAttendants) {

      const getAttendants = await getAttendant(guid(), keyOrigin);

      if (getAttendants.data.status !== 'failure') {



        getAttendants.data.resource.items.forEach(async (rulesItems) => {
          await setAttendant(guid(), info, rulesItems.identity, rulesItems.teams);
        });

        showMessage('Successfully cloned attendants', 'success', 'Done!');
        setLoading(false)
        setCheckedAttendants(false)

      } else {
        setLoading(false)
        showMessage('Error cloning attendants', 'danger', 'Notice!');
      }

    }

    if (checkedReplies) {

      const { data: { status } } = await handleGetReplies(guid(), keyOrigin);

      if (status !== 'failure') {

        const { data: { resource: { items } } } = await handleGetReplies(guid(), keyOrigin);

        items.forEach(async (e) => {

          const { data: { resource } } = await handleGetReplies(guid(), keyOrigin, e.id)
          delete resource.total
          await handleSetReplies(resource, info, e.id)

        })
        showMessage('Your answers have been successfully cloned', 'success', 'Done!');
        setLoading(false)
        setCheckedReplies(false)
      } else {

        showMessage('Error cloning your answers', 'danger', 'Done!');
        setLoading(false)
        setCheckedReplies(false)

      }

    }




  }

  function showMessage(msg, type, status) {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })

    }, 900);

  }


  function setLocaStorageKey1(e) {

    setKeyOrigin(e)
    localStorage.setItem('keyOne', e)

  }

  function clear() {

    localStorage.removeItem('keyOne');


    setKeyOrigin('');
    setKeyTarget('');
  }

  const page = (

    <div className="fadeIn animated" style={{ padding: '30px' }}>
      <div className="home">
        <div className="">
          <form className="">
            <div className="form-group">
              <input value={keyOrigin}
                onChange={(event) => setLocaStorageKey1(event.target.value)}
                type="text" name="origem" className="bp-input-wrapper blip-input w-100"
                placeholder="Enter the source bot key." required />
            </div>


            <div className="container-switch">

              <label>
                <span>Builder: </span>
                <Switch onChange={handleChangeBuilder}
                  checked={checkedBuilder} onColor="#3f7de8" />
              </label>
              <label>
                <span>Resource: </span>
                <Switch onChange={handleChangeResource}
                  checked={checkedResource} onColor="#3f7de8" />
              </label>
              <label>
                <span>Queue Management: </span>
                <Switch onChange={handleChangeRules}
                  checked={checkedRules} onColor="#3f7de8" />
              </label>
              <label>
                <span>Attendants: </span>
                <Switch onChange={handleCheckAttendants}
                  checked={checkedAttendants} onColor="#3f7de8" />
              </label>
              <label>
                <span>Ready Answers: </span>
                <Switch onChange={handleChangeReplies}
                  checked={checkedReplies} onColor="#3f7de8" />
              </label>

            </div>

            <div className="container-buttons">
              <button type="submit" onClick={start} style={{ marginRight: "10px" }} className="bp-btn bp-btn--bot bp-btn--small" disabled={!keyOrigin}>

                {loading && (
                  <BlipLoading />
                )}
                {loading && <span>Cloning</span>}
                {!loading && <span>Clone</span>}

              </button>
              <button type="button" onClick={clear} style={{ marginRight: "10px" }} className="bp-btn bp-btn--bot bp-btn--small">Clear</button>



            </div>
          </form>
        </div>
      </div>
      <NotificationContainer />
    </div>


  )
  return (
    <div className="">
      {page}
    </div>
  );
}


export default Clone;

