import api from '../baseUrl';

export default (guid, key, tunnel) => {

 
  return api.post('/commands', { 
    "id": guid, 
    "to": "postmaster@desk.msging.net", 
    "method": "get", 
    "uri": `/tickets?$filter=(CustomerIdentity%20eq%20'${tunnel}')%20and%20(status%20eq%20'Open'%20or%20status%20eq%20'Waiting')` 
  }, {
    headers: {
      'Content-Type': 'application/json',
      'Authorization': key
    }
  });
}