import React, { useState } from 'react';
import { BlipSelect } from '../BlipSelect/index'

import Answer from '../BlipAnswerResource/answerResouce';
import MenuResource from '../BlipMenuResource/menuResource';
import ConfigResource from '../BlipConfigResource/configResource';
import TitleMenu from '../BlipTitleMenu/titleMenuResouce';


function Resource({ info }) {

  const [config, setConfig] = useState('Select a Content');
  const [key, setKey] = useState(info || '');

  const data = [
    { value: 'stateless-answer', label: 'Stateless Answer' },
    { value: 'config', label: 'Config' },
    { value: 'menu', label: 'Menu' }
  ];

  return (

    <>
      <div className="container w-100">
        <div className="row">
          <div className="col-6">
            <label>bot key: </label>
            <input
              name="key"
              type="text"
              className="bp-input-wrapper blip-input w-100"
              placeholder="Digite Key do bot"
              value={info}

              required disabled={true}
            />
          </div>
          <div className="col-6">
            <label>Select a Content: </label>
            <BlipSelect options={data} onSelectOption={($event) => setConfig($event['$event'].optionProps.value)} placeholder={config} />
          </div>
          <div className="w-100">
            {
              config === 'stateless-answer' ? <Answer chave={info} /> : config === 'config' ? <ConfigResource chave={info} /> : config === 'menu' ? <MenuResource chave={info} /> : ''
            }
          </div>
        </div>
      </div>
    </>

  )

}


export default Resource;