import React, { useState, useEffect } from "react";
import handleSetResource from "../../services/handleSetResource";
import handleGetResourceAll from "../../services/handleGetResourceAll";
import handleGetResourceItems from "../../services/handleGetResourceItems";


import guid from '../../utils/guid';
import { BlipLoading } from '../BlipLoading';
import { showToast } from "../../api/commomService";
import MenuPreview from "../BlipMenuPreview/index";
import Select from '../Select/index';


function MenuResource({ chave }) {

  const [inputList, setInputList] = useState([{ opcao: "", estadoDestino: "", menu: false, valida: false, time: null }]);
  const [chaveRecurso, setChaveRecurso] = useState('');
  const [loading, setLoading] = useState(false);
  const [mensager, setMensager] = useState('Select text');
  const [resourceAll, setResourceAll] = useState([]);
  const [supportTeam, setSupportTeam] = useState('');
  const [listName, setListName] = useState('');


  const getResourceAll = async () => {

    const response = await handleGetResourceAll(guid(), chave);

    const menu = [];

    if (response.data.resource.items.length > 0) {

      response.data.resource.items.map((recurso) => {

        if (recurso.split('_')[0] === 'menu' && !recurso.includes('resposta')) {
          menu.push({
            id: recurso,
            value: recurso,
            label: recurso,
            selected: false
          })
        }
      });
      menu.push({
        id: 'New Resource',
        value: 'New Resource',
        label: 'New Resource',
        selected: false
      })
      setResourceAll(menu)
    }


  }


  useEffect(() => {
    getResourceAll();
  }, [])


  const handleMensager = (e) => {
    const { value } = e.target;
    setMensager(value);
  }


  const handleInputChange = (e, index) => {

    const { name, value } = e.target;

    const list = [...inputList];
    const isTrueSet = name === 'menu' || name === 'valida' ? (value === 'true') : value;
    list[index][name] = isTrueSet;
    setInputList(list);
  };

  const handleChaveRecursoChange = (e) => {

    const { value } = e.target;
    setChaveRecurso(value);
  };

  const handleRemoveClick = index => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };


  const handleSupportTeam = (e, index) => {

    const { name, value } = e.target;
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
    setSupportTeam(value);


  }
  const handleAddClick = () => {
    setInputList([...inputList, { opcao: "", estadoDestino: "", menu: false, valida: false, time: null }]);
  };

  const handleRecordResource = async () => {

    setLoading(true);
    const payloadMenuResource = { menu: [...inputList], listName };

    payloadMenuResource.menu.map((e) => {

      if (e.menu === "false" || e.menu === false) {
        e.nextStep = {
          estadoDestino: e.estadoDestino,
          menu: false
        }
      }

      return e;
    });


    const getKey = chave;
    const result = await handleSetResource(guid(), getKey, "application/json", chaveRecurso, payloadMenuResource);

    const answer = `${chaveRecurso}_resposta`
    const result2 = await handleSetResource(guid(), getKey, "text/plain", answer, mensager);


    if (result.data.status === 'success') {
      showMessage('Resource successfully registered!', 'success', 'Done!');
      return false;
    } else {
      showMessage('Error registering a resource!', 'danger', 'Error!');
      return false;
    }
  };

  const handleListName = (e) => {

    const { value } = e.target;
    setListName(value);

  }

  const handleResourceChange = async (e) => {

    if (e !== 'New Resource') {
      const { menu, listName } = await (await handleGetResourceItems(guid(), chave, e)).data.resource;
      const resultResourceItemAnswer = await (await handleGetResourceItems(guid(), chave, `${e}_resposta`)).data.resource;

      setMensager(resultResourceItemAnswer);
      setChaveRecurso(e);
      setInputList(menu);
      setListName(listName)
      return false;
    }

    setMensager('Select Text');
    setChaveRecurso('');
    setListName('')
    setInputList([{ opcao: "", estadoDestino: "", menu: false, valida: false, time: null }]);
  }

  function showMessage(msg, type, status) {


    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);

    }, 900);

  }


  return (
    <div className="container w-100">

      <h1 >Menu Registration</h1>

      
 
      <div className="row">

      <div className="col-12">
          <label>Search  in resource base:</label>
        </div>
        <div className="col-4">
          <Select
            name="template"
            label="Search"
            icon="filter"
            placeholder="Selecione.."
            multiple={false}
            searchable={true}
            options={resourceAll}
            selected={resourceAll}
            onChange={($event) => handleResourceChange($event)}
          />

        </div>

        <div className="col-12">
          <label>Resource key:</label>
        </div>  
        <div className="col-8">
          <input
            name="chave"
            className="bp-input-wrapper blip-input small"
            placeholder="Enter Resource Key"
            value={chaveRecurso}
            onChange={e => handleChaveRecursoChange(e)}
          />
        </div>
      </div>

      <div className="row">
        <div className="col-12">
          <label>Menu Title:</label>
        </div>
        <div className="col-8">
          <textarea
            className="bp-textarea-wrapper blip-textarea w-100"
            rows="5"
            name="estadoDestino"
            placeholder="Enter your message"
            value={mensager}
            onChange={e => handleMensager(e)}
          />
        </div>

        <div className="col-3">
          <label>List name WhatsApp:</label>
          <input
            type="text"
            className="bp-input-wrapper blip-input small "
            name="time"
            placeholder="Enter the list name"
            value={listName}
            onChange={e => handleListName(e)}
          />
        </div>
      </div>


      {inputList.map((x, i) => {
        return (
          <div className="mt-2 mb-2" key={i.toString()}>
            <div className="row">
              <div className="col">
                <label>Menu option - {i + 1}:</label>
                <input
                  name="opcao"
                  type="text"
                  className="bp-input-wrapper blip-input small "
                  placeholder="Enter the menu option"
                  value={x.opcao}
                  onChange={e => handleInputChange(e, i)}
                />
              </div>
              <div className="col">
                <label>Destination State - {i + 1}:</label>
                <input
                  className="bp-input-wrapper blip-input small "
                  name="estadoDestino"
                  type="text"
                  placeholder="Enter the Destination state"
                  value={x.estadoDestino}
                  onChange={e => handleInputChange(e, i)}
                />
              </div>
              <div className="col">
                <label>Menu Type:</label>
                <select name="menu" value={x.menu} onChange={e => handleInputChange(e, i)} className="bp-input-wrapper blip-select small">
                  <option value="select">Select</option>
                  <option value="true">true</option>
                  <option value="false">false</option>
                </select>
              </div>
              <div className="col">
                <label>Validate answer:</label>
                <select name="valida" value={x.valida} onChange={e => handleInputChange(e, i)} className="bp-input-wrapper blip-select small">
                  <option value="select">Select</option>
                  <option value="true">true</option>
                  <option value="false">false</option>
                </select>
              </div>
              <div className="col-3">
                <label>Enter a support team:</label>
                <input
                  type="text"
                  className="bp-input-wrapper blip-input small "
                  name="time"
                  placeholder="Enter a support team"
                  value={x.time}
                  onChange={e => handleSupportTeam(e, i)}
                />
              </div>

              <div className="col-3">
                <label>Action:</label>
                <div className="box">
                  {inputList.length !== 1 && <button
                    className="bp-btn bp-btn--delete bp-btn--small"
                    onClick={() => handleRemoveClick(i)}>Remove</button>}
                </div>
              </div>

            </div>
            <hr />
          </div>
        );
      })}

      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <button onClick={handleAddClick} className="bp-btn bp-btn--bot bp-btn--small">Add Option</button>


        <MenuPreview data={inputList} text={mensager} />



        <button onClick={handleRecordResource} className="bp-btn bp-btn--bot bp-btn--small">
          {loading && (
            <BlipLoading />
          )}
          {loading && <span>Save</span>}
          {!loading && <span>Save</span>}
        </button>




      </div>


    </div>

  );
}

export default MenuResource;


