import api from '../baseUrl';

export default (guid, key, email, team ) => {

  return api.post('/commands',{

    "id": guid,
    "to": "postmaster@desk.msging.net",
    "method": "set",
    "uri": "/attendants",
    "type": "application/vnd.iris.desk.attendant+json",
    "resource": {
      "identity":  `${email}`,
        "teams": team
    }
},{
      headers: {
        'Content-Type': 'application/json',
        'Authorization': key
      }});
}