import React, { useState, useEffect } from 'react';
import { BlipSelect } from '../BlipSelect/index'

import getMessageTemplate from '../../services/getMessageTemplate'
import guid from '../../utils/guid';
import TemplatePreview from '../BlipTemplateMessage/index';
import { BlipLoading } from '../BlipLoading';
import { showToast } from "../../api/commomService";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import sendMessageWhatsApp from '../../services/sendMessageWhatsApp';
import getStateBot from '../../services/getStateBot';
import { markdownToHtml, textReplace } from '../../utils/string'
import setTrackingEventBroadCast from '../../services/setTrackingEventBroadCast'
import Switch from "react-switch";
import moment from 'moment';
import timezone from 'moment-timezone';
import getTicketOpen from '../../services/getTicketOpen';
import getTunnelByPhone from '../../services/getTunnelByPhone';

const SendNotification = ({ shortName, accessKey1, email }) => {


  const [config, setConfig] = useState({ value: "No message selected", config: "" });
  const [data, setData] = useState([]);
  const [dataSubBot, setDataSubBot] = useState([]);
  const [dataLanguage, Language] = useState([{ label: 'Portuguese', value: 'pt_BR' }, { label: 'English', value: 'en_US' }]);
  const [message, setMessage] = useState([])
  const [template, setTemplate] = useState('');
  const [stateId, setStateId] = useState('');
  const [flowId, setFlowId] = useState('');
  const [filterLanguage, setFilterLanguage] = useState('');
  const [inputList, setInputList] = useState([{ field: "", value: "" }]);
  const [inputListTemplate, setInputListTemplate] = useState([{ value: "" }]);
  const [loading, setLoading] = useState(false);
  const [phone, setPhone] = useState('');
  const [templateName, setTemplateName] = useState('Select a template');
  const [language, setLanguage] = useState('Select a language');
  const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
  const [templateSubBot, setTemplateSubBot] = useState('Select a template');
  const [identity, setIdentity] = useState('');
  const [dataFlowId, setDataFlowId] = useState({ label: "No message selected", value: "" });
  const [templateSubBotFlowId, setTemplateSubBotFlowId] = useState('Select a state');
  const [subBotConsultAttendence, setSubBotConsultAttendence] = useState('')

  const fnAuxTemplates = async () => {

    const arrayTemplates = [];

    const response = await getMessageTemplate(guid(), localStorage.getItem('info'));


    const templates = response.data.resource.data.filter((t) => t.name && t.language === filterLanguage && t.name.split('_')[0] !== 'sample' && t.status === 'APPROVED');

    templates.map((template) => {
      arrayTemplates.push({ label: template.name, value: template.components })
    });

    setData(arrayTemplates);
    setMessage(templates);


  }


  const fnAuxSubBots = () => {

    const arraySubBots = [];
    subBots.map((bots) => { arraySubBots.push({ label: bots.longName, value: bots.longName }); })
    setDataSubBot(arraySubBots);

  }


  useEffect(() => {

    fnAuxTemplates()

  }, [filterLanguage]);



  useEffect(() => {

    fnAuxSubBots()

  }, []);


  const handleInputChange = (e, index) => {

    const { name, value } = e.target;

    const list = [...inputList];
    const isTrueSet = name === 'menu' || name === 'valida' ? (value === 'true') : value;
    list[index][name] = isTrueSet;
    setInputList(list);
  };

  const handleInputChangeTemplate = (e, index) => {


    const { name, value } = e.target;

    const list = [...inputListTemplate];
    const isTrueSet = name === 'menu' || name === 'valida' ? (value === 'true') : value;
    list[index][name] = isTrueSet;
    setInputListTemplate(list);
  };


  const handleRemoveClick = index => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };


  const handleAddClick = () => {
    setInputList([...inputList, { field: "", value: "" }]);
  };


  const handleSendMessage = async () => {
    setLoading(true);

    const { accessKey, identifier } = subBots[0];
    const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));

    const tunnel = await getTunnelByPhone(guid(), KEY, phone);

    if (tunnel.data.resource.items.length > 0) {

      const tunnelId = tunnel.data.resource.items[0].identity;
      const hasTicketOpen = await getTicketOpen(guid(), KEY, tunnelId);

      if (hasTicketOpen.data.resource.items.length > 0) {

        const {sequentialId, team} = hasTicketOpen.data.resource.items[0]

        showMessage(`The ticket #${sequentialId} is already open with ${team}`, 'warning');

        return false;

      }

    }
    if (!phone) {

      showMessage('You need to enter a phone', 'danger');
      setLoading(false);
      return false;

    }


    const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));
    let params = {};

    inputListTemplate.map((e, idx) => {
      params['var' + (idx + 1)] = e.value;
    });

    inputList.map((e) => {
      params[e.field] = e.value;
    });


    const payload = {
      "telephone": phone,
      "template": config.label,
      params,
      "flowId": flowId,
      'masterState': identity,
      "stateId": stateId,
      "languageCode": filterLanguage,
      "namespace": resultNameSpace.data.resource['message_template_namespace'],
      "trackOrigin": true,
      "checkAttendance": true,
      "senderEmail": email,
    }

    console.log(payload, shortName, accessKey1);
    await sendMessageWhatsApp(payload, shortName, accessKey1);
    setLoading(false);
    let action = `${payload.telephone}|${payload.template}|${email}|${moment().tz('America/Sao_Paulo').format('YYYY-MM-DD HH:mm:ss')}`;
    await setTrackingEventBroadCast(guid(), localStorage.getItem('info'), action);
    showMessage('Message sent successfully', 'success');

  }


  function showMessage(msg, type, status) {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);

    }, 900);

  }

  const handlePhoneChange = (e) => {
    const { value } = e.target;
    setPhone(value);
  }

  const handleSetEvent = (event) => {

    const config = event['$event'].optionProps;
    const label = event['$event'].optionProps.label;

    console.log(config)

    setConfig(config);
    setTemplateName(label);

    let variables = [];
    let arr = [];
    let index = 0;

    if (config.value.length > 0) {

      variables = config.value.map((e) => {

        return e.text !== undefined ? e.text.match(/{{2}\d{0,2}}{2}/igm) : '';

      }).join().split(',').filter((e) => e !== '');

      for (const iterator of variables) {

        arr.push({ value: '' });

      }
      setInputListTemplate(arr);
    }


  }



  const handleSetEventLanguage = (event) => {

    const filterLangue = event['$event'].optionProps.value;
    const label = event['$event'].optionProps.label;


    setFilterLanguage(filterLangue);
    setLanguage(label);

  }


  const handleSetEventSubBot = async (event) => {


    const nameSubBot = event['$event'].optionProps.label;
    setTemplateSubBot(nameSubBot);

    const { identifier, accessKey, identity } = subBots.filter((e) => e.longName === nameSubBot)[0];
    const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));
    const resultHttp = await getStateBot(KEY, shortName, accessKey);
    const data = resultHttp.data.statesInformation;

    let arrayStates = [];

    for (const iterator of data) {

      arrayStates.push({ label: iterator.name, value: iterator.id })

    }
    setDataFlowId(arrayStates);
    setFlowId(resultHttp.data.flowId);
    setIdentity(identity);
  }
  const handleSetEventSubBotFlowId = async (event) => {


    const nameOfState = event['$event'].optionProps.label;
    const nameOfStateValue = event['$event'].optionProps.value;
    setTemplateSubBotFlowId(nameOfState);
    setStateId(nameOfStateValue);

  }
  return (

    <>
      <div className="container w-100">
        <div className="row">
          <div className="col-6">
            <label>Phone: </label>
            <input
              name="key"
              type="text"
              className="bp-input-wrapper blip-input w-100"
              placeholder="Enter the customer's phone. example: 11970950011"
              value={phone}
              onChange={e => handlePhoneChange(e)}

            />
          </div>
          <div>{template}</div>

          <div className="col-3">
            <label>Language: </label>
            <BlipSelect options={dataLanguage} onSelectOption={($event) => handleSetEventLanguage($event)} placeholder={language} />
          </div>


          <div className="col-3">
            <label>Select a template: </label>
            <BlipSelect options={data} onSelectOption={($event) => handleSetEvent($event)} placeholder={templateName} />
          </div>


          <div className="w-100">

          </div><br></br>
          <div className="col-6">
            <TemplatePreview data={config} templateValue={inputListTemplate} />
          </div>

          <div className="col-6">
            {inputListTemplate.map((x, i) => {
              return (
                <div className="mt-2 mb-2" key={i.toString()}>
                  <div class="row">
                    <div class="col">
                      <label>Template Value - {i + 1}:</label>
                      <input
                        type="text"
                        name="value"
                        className="bp-input-wrapper blip-input w-100"
                        placeholder="value"
                        value={x.value}
                        onChange={e => handleInputChangeTemplate(e, i)}
                      />
                    </div>
                  </div>
                </div>
              );
            })}


          </div>
          <br></br>
          <div className="col-6">
            <div className="col-12">
              <label>Select a chatbot:</label>
            </div>
            <BlipSelect options={dataSubBot} onSelectOption={($event) => handleSetEventSubBot($event)} placeholder={templateSubBot} />
          </div>
          <div className="col-6">
            <div className="col-12">
              <label>Select chatbot block:</label>
            </div>
            <BlipSelect options={dataFlowId} onSelectOption={($event) => handleSetEventSubBotFlowId($event)} placeholder={templateSubBotFlowId} />
          </div>
          <hr />
          <div className="container w-100">
            <hr />
            <br />
            <br />
            <div className="row">
              <div className="col-12">
                <label>Contact Extras:</label>
              </div>
              <div className="col-12">

              </div>
            </div>

            {inputList.map((x, i) => {
              return (
                <div className="mt-2 mb-2" key={i.toString()}>
                  <div class="row">
                    <div class="col">
                      <label>Field Extra - {i + 1}:</label>
                      <input
                        type="text"
                        name="field"
                        className="bp-input-wrapper blip-input w-100"
                        placeholder="Field"
                        value={x.opcao}
                        onChange={e => handleInputChange(e, i)}
                      />
                    </div>
                    <div class="col">
                      <label>Value Extra - {i + 1}:</label>
                      <input
                        type="text"
                        className="bp-input-wrapper blip-input w-100"
                        name="value"
                        placeholder="Value"
                        value={x.estadoDestino}
                        onChange={e => handleInputChange(e, i)}
                      />
                    </div>
                    <div class="col-6">
                      <label>Action:</label>
                      <div className="box">
                        {inputList.length !== 1 && <button
                          className="bp-btn bp-btn--delete bp-btn--small"
                          onClick={() => handleRemoveClick(i)}>Remove</button>}
                      </div>
                    </div>
                  </div>
                  <hr />
                </div>
              );
            })}


            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <button onClick={handleAddClick} className="bp-btn bp-btn--bot bp-btn--small">Add Extras</button>
              <button onClick={handleSendMessage} className="bp-btn bp-btn--bot bp-btn--small">
                {loading && (
                  <BlipLoading />
                )}
                {loading && <span> Send Message</span>}
                {!loading && <span> Send Message</span>}
              </button>

            </div>


          </div>

        </div>
      </div>
    </>

  )

};

export default SendNotification;